import React, { useState } from "react";
import Order from "./Order";

export default function Orders(props) {
  const productListSize = props.products.length;
  console.log(productListSize);

  const [priceFilterText, setPriceFilterText] = useState("");
  const [itemFilterText, setItemFilterText] = useState("");
  const products = [...props.products];
  const [productsist, setProductsList] = useState(products);

  return (
    <>
      <h2>Orders</h2>
      <label>Price:</label>
      <input
        type="text"
        value={priceFilterText}
        onChange={(event) => setPriceFilterText(event.target.value)}
      ></input>
      <label>Item:</label>
      <input
        type="text"
        value={itemFilterText}
        onChange={(event) => setItemFilterText(event.target.value)}
      ></input>
      <button
        onClick={(event) =>
          setProductsList(
            props.products.filter(
              (product) =>
                (priceFilterText == "" || product.price == priceFilterText) &&
                (itemFilterText == "" || product.item == itemFilterText)
            )
          )
        }
      >
        Filter
      </button>
      <div>
        <table border="1px">
          <tr>
            <th>Id</th>
            <th>Item</th>
            <th>Qty</th>
            <th>Size</th>
            <th>Price</th>
          </tr>
          {/* <tr>
            <td>{props.products[0].id}</td>
            <td>{props.products[0].item}</td>
            <td>{props.products[0].qty}</td>
            <td>{props.products[0].size}</td>
            <td>{props.products[0].price}</td>
          </tr> */}
          {/* <tr> */}
          {productsist
            // .filter(
            //   (product) =>
            //     priceFilterText != "" && product.price == { priceFilterText }
            // )
            .map((product) => (
              <Order key={product.id} {...product} />
            ))}
          {/* </tr> */}
        </table>
      </div>
    </>
  );
}
